body {
     font-family:$font-poppins;
     font-size: 16px;
     background-color:$color-white;
     margin: 0;
     font-weight: 400;
     background: $color-black;     
     a {
         text-decoration: none !important;
    }
     *:focus, select:focus, option:focus {
         outline: none !important;
    }
}
